<template>
  <!--begin::Tiles Widget 12-->
  <div class="card card-custom bgi-no-repeat bgi-size-cover gutter-b w-100">
    <router-link :to="{ path: '/builder', query: { task: this.task_name } }">
      <div class="card-body">
        <div>
          <img style="height:48px" :src="this.task_logo" />
        </div>
        <div
          v-if="task_name == 'ABSA'"
          class="text-dark font-weight-bolder font-size-h3 mt-3"
        >
          {{ task_info.full_name }}
        </div>
        <div v-else class="text-dark font-weight-bolder font-size-h2 mt-3">
          {{ task_info.full_name }}
        </div>

        <p
          class="text-muted font-weight-bold font-size-lg mt-1 bottom-align-text"
        >
          {{ task_info.available_domain.length }} domain transformations<br />
          {{ task_info.available_ut.length }} universal transformations
        </p>
      </div>
    </router-link>
  </div>
</template>

<script>
export default {
  name: "widget-task",
  props: {
    task: String
  },
  data() {
    var task_info = require("../../../../../public/Tasks/" +
      this.task +
      "/task_description.json");
   
    return {
      task_name: this.task,
      task_info: task_info,
      task_logo: "media/logos/task_logo/" + this.task + ".jpg"
    };
  }
};
</script>
